
.bike {
  &__photo-container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 320px;
    overflow: hidden;

    transition: transform $duration $animation-timing-function;

    &:hover {
      transform: scale(1.05);
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onTablet {
      height: 188px;
      padding-bottom: 0;
    }

    @include onDesktop {
      height: 320px;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;

    
  }

  &__photos {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }
}
